






























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			type:0,
			malllist:[
			],
			typeList:[],
			mallnum:0,
			mallprice:0,
			pop:false,
			storeid:'',
			classid:'',
			class_id:'',
			tenant:{},
			mallname:'',
			mallname1:''
		}
	},
	onLoad(opt) {
		this.tenant=wx.getStorageSync('tenant');
		const length=this.tenant.name.split('').length;
		this.mallname=this.tenant.name.substring(0,parseInt(length/2));
		this.mallname1=this.tenant.name.substring(parseInt(length/2),length)
		wx.setStorageSync('tenant','');
		this.static=ut.static;
		this.classid=opt.classid;
		this.storeid=opt.storeid;
		this.req_storeclasslist()
	},
	methods: {
		cg_pop(){
			if(!this.pop&&!this.mallnum){
				return;
			}
			this.pop=!this.pop;
		},
		cg_type(type){
			this.type=type;
			this.class_id=this.typeList[type].id;
			this.req_goodslist();
		},
		go_build_pay(){
			wx.navigateTo({
				url: '../build/pay'
			})
		},
		go_build_mallinf(id){
			wx.navigateTo({
				url: `../build/mallinf?_id=${id}`
			})
		},
		minus(index){
			this.malllist[index].mallnum-=1;
			this.mallnum-=1;
			this.mallprice=(Number(this.mallprice)-Number(this.malllist[index].price)).toFixed(2);
			if(this.mallnum==0){
				this.pop=false;
			}
		},
		add(index){
			this.malllist[index].mallnum+=1;
			this.mallnum+=1;
			this.mallprice=(Number(this.mallprice)+Number(this.malllist[index].price)).toFixed(2);
		},
		malllistmap(arr){
			return arr.map((item)=>{
				item.price=Number(item.price).toFixed(2);
				item.mallnum=0;
				return item;
			})
		},
		cleanbar(){
			this.mallnum=0;
			this.mallprice=0;
			this.malllist=this.malllistmap(this.malllist);
			this.pop=false;
		},
		req_storeclasslist(){
			ut.request({
				data: {
					storeid:this.storeid
				},
				url: "goods/storeclasslist"
			}).then(data=>{
				this.typeList=data;
				if(data[0]){
					this.class_id=data[0].id;
				}
			}).then(data=>{
				this.req_goodslist();
			})
		},
		req_goodslist(){
			ut.request({
				data: {
					storeid:this.storeid,
					classid:this.classid,
					class_id:this.class_id
				},
				url: "goods/goodslist"
			}).then(data=>{
				data.forEach(item=>{
					item.num=0;
				})
				this.malllist=data;
			})
		}
	}
}
